import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { User } from '../../../../../models/user';
import * as fromRoot from '../../../../../reducers';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import * as usersAction from '../../../../../actions/users';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  user$: Observable<User>;
  status$: Observable<any>;

  loading = false;
  titre$ = 'User Profile';
  userId: number;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private _store: Store<fromRoot.State>) {
    this.user$ = _store.select(fromRoot.getUser);
    this.status$ = _store.select(fromRoot.getUserStatus);
    this.loading = true;
    this.route.params.subscribe(params => {
      this.userId = params['id'];
      console.log(this.userId);
    });
    this.status$.subscribe(val => {
      if (val === true) {
        this.loading = true;
        // tslint:disable-next-line:curly
      } else
        this.loading = false;
    });

  }

  ngOnInit() {
    console.log('uSerid', this.userId);
    this._store.dispatch(new usersAction.RequestUser(this.userId));
  }

}
