import { Component, OnInit } from '@angular/core';
import { User } from '../../../../../models/user';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';

import * as usersAction from '../../../../../actions/users';

import * as fromRoot from '../../../../../reducers';

@Component({
  selector: 'app-users-page',
  templateUrl: './users-page.component.html',
  styleUrls: ['./users-page.component.css']
})
export class UsersPageComponent implements OnInit {

  users$: Observable<User[]>;
  titre$ = 'Users';
  loading = false;
  status$: Observable<any>;


  constructor(private _store: Store<fromRoot.State>) {
    this.users$ = _store.select(fromRoot.getUsers);
    this.status$ = _store.select(fromRoot.getUserStatus);
    this.loading = true;
    this.status$.subscribe(val => {
      if (val === true) {
        this.loading = true;
        // tslint:disable-next-line:curly
      } else
        this.loading = false;
    });
  }

  ngOnInit() {
    this._store.dispatch(new usersAction.RequestUsers());
  }

}
